<template>
   <div class="searchPage">
      <title-bar :titleData='titleData'></title-bar>
      <div class="aticles">
			<articleItem :item-data='item' v-for="(item,index) in aticles" :key="index"></articleItem>
		</div>
      <Pagination background layout="prev, pager, next" :page-size="4" :total="totalPage"
						@current-change = pageChange
		></Pagination>
   </div>
</template>

<script type="text/ecmascript-6">
import titleBar from '../components/titleBar'
import articleItem from "../components/aticleItem"
import { sessionData } from '../js/public'
import { Pagination } from 'element-ui';
export default {
   data() {
      return {
         titleData:{
            titleTag:'搜索结果为',
            titleMotto:'不要轻易放弃。学习成长的路上，我们长路漫漫，只因学无止境。'
         },
         aticles:[
            {
               date: "2018-06-12",
               imgIndex: 2,
               text: "我创建的第一条数据",
               time: "18:29:13",
               title: "第一条数据",
            }
         ],
         totalPage:0,
         param:{
				page_no: '1',
				page_size: '4'
			}
      }
   },
   created () {
      this.getSearchData()
   },
   methods: {
      pageChange(val){
			this.param.page_no = String(val)
			this.getDatas()
      },
      
      getSearchData(){
         this.param.title = sessionData('searchArticle')
         this.$http.post('/home/search/',this.param).then(ret =>{
				this.totalPage = ret.data.total
				this.aticles = ret.data.content
			})
      },
   },
   components: {
      titleBar,articleItem,Pagination
   }
}
</script>

<style scoped lang="scss" scoped>
.searchPage{
   .aticles{
      margin-top: 30px
   }
}
</style>
