<template>
   <div class="learn">
      <title-bar :titleData='titleData'></title-bar>
      <div class="tags">
         <div class="tagItem" v-for="item in tags" :key="item.tag_id" @click='searchTagArticle(item)'>{{item.tag_name}}</div>
      </div>
      <div class="aticles">
			<articleItem :item-data='item' widthObj = '94%' v-for="(item,index) in aticles" :key="index"></articleItem>	
      </div>
		<Pagination background layout="prev, pager, next" :page-size="Number(param.page_size)" 
                  :total="totalPage" @current-change = pageChange>
      </Pagination>
   </div>
</template>

<script type="text/ecmascript-6">
import { Pagination } from 'element-ui';
import titleBar from '../../components/titleBar'
import articleItem from "../../components/aticleItem"
export default {
   data() {
      return {
         titleData:{
            titleTag:'学无止境',
            titleMotto:'不要轻易放弃。学习成长的路上，我们长路漫漫，只因学无止境。'
         },
         tags:[],
         aticles:[],
         totalPage:0,
			param:{
				page_no: '1',
				page_size: '6'
			}
      }
   },
   created () {
      this.getTags()
      this.getArticles()
   },
   methods: {
      searchTagArticle(item){
         this.param.tag_id = item.tag_id
         this.$http.post('/learn/tag-articles/',this.param).then(ret =>{
				this.totalPage = ret.data.total
				this.aticles = ret.data.content
			})
      },
      pageChange(val){
			this.param.page_no = String(val)
			this.getArticles()
		},
      getArticles(){
			this.$http.post('/home/aticles/',this.param).then(ret =>{
				this.totalPage = ret.data.total
				this.aticles = ret.data.content
				this.$nextTick(()=>{
					document.body.scrollTop = 0;
					document.documentElement.scrollTop = 0;
				})
			})
		},
      getTags(){
         this.$http.get('/learn/tags/',{}).then( ret => {
            this.tags = ret.data
         })
      },
   },
   components: {
      Pagination,articleItem,titleBar
   }
}
</script>

<style scoped lang="scss" scoped>
.learn{
   .tags{
      padding: 30px 0 30px 100px;
      >div{
         display: inline-block;
         cursor: pointer;
         font-size: 16px;
         color: #fff;
         line-height: 36px;
         padding: 0 26px;
         margin: 0 4px 4px 0;
         opacity: .8;
         &:hover{
            opacity: 1;
         }
         &:nth-child(5n-4){
            background-color: rgb(246, 80, 135);
         }
         &:nth-child(5n-3){
            background-color: rgb(50, 151, 104);
         }
         &:nth-child(5n-2){
            background-color: rgb(251, 128, 82);
         }
         &:nth-child(5n-1){
            background-color: rgb(54, 147, 202);
         }
         &:nth-child(5n){
            background-color: rgb(104, 201, 153);
         }
      }
   }
}
</style>
