<script type="text/ecmascript-6">
import titleBar from '../../components/titleBar'
export default {
   data() {
      return {
         titleData:{
            titleTag:'留言板',
            titleMotto:'你，生命中最重要的过客，之所以是过客，因为你未曾为我停留。'
         },
      }
   },
   mounted () {
      this.$nextTick(()=>{
         var canvas = document.getElementById("myCanvas");
         if(canvas.getContext){  
            //获取对应的CanvasRenderingContext2D对象(画笔)
            var ctx = canvas.getContext("2d");  
            
            //开始一个新的绘制路径
            ctx.beginPath();
            //设置弧线的颜色为蓝色
            ctx.strokeStyle = "red";
            var circle = {
                  x : 19,    //圆心的x轴坐标值
                  y : -14,    //圆心的y轴坐标值
                  r : 25      //圆的半径
            };
            //沿着坐标点(100,100)为圆心、半径为50px的圆的顺时针方向绘制弧线
            ctx.arc(circle.x, circle.y, circle.r, 0, 2*Math.PI, false);    
            //按照指定的路径绘制弧线
            ctx.stroke();
         }
      })
   },
   methods: {
      login(){
         
      },
      changeHover(e,type){
         if( type == 'emoji'){
            e.currentTarget.classList.add('icon-biaoqing')
         }else{
            e.currentTarget.classList.remove('icon-tupian1')
            e.currentTarget.classList.add('icon-tupian')
         }
      },
      changeLeave(e,type){
         if( type == 'emoji'){
            e.currentTarget.classList.remove('icon-biaoqing')
         }else{
            e.currentTarget.classList.add('icon-tupian1')
            e.currentTarget.classList.remove('icon-tupian')
         }
      },
   },
   components: {
      titleBar
   }
}
</script>

